.nav {
  background: rgba(0,0,0,0);
  width: max-content;
  display: block;
  padding: 0.7rem 1.7rem;
  z-index: 2;
  position: fixed;
  left: 15%;
  transform: translate(50%);
  bottom: 1rem;
  display: flex;
  gap: 0.8rem;
  border-radius: 1rem;
  backdrop-filter: blur(15px);
}

.nav a {
  background: transparent;
  padding: 0.9rem;
  border-radius: 50%;
  display: flex;
  color: white;
  font-size: 1.1rem;
}

.nav a:hover {
  background: rgba(0,0,0,3);
}

.nav a.active {
  background: var(--colour-bg);
  color: white
}

@media screen and (max-width: 1024px) {
  .nav {
    left: -18%
  }
}

@media screen and (max-width: 600px) {
  .nav {
    left: -45%
  }
}