*{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

.root {
    --colour-bg: #1f1f38;
    --colour-bg-variant: #2c2c6c;
    --colour-primary: #4db5ff;
    --colour-primary-variant: rgba(77, 181, 255, 0.4);
    --colour-white: #fff;
    --colour-light: rgba(255, 255, 255, 0.6);

    --container-width: 75%;
    --container-width-medium: 86%;
    --container-width-big: 90%;

    --transition: all 400ms ease
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none
}

body {
    background: var(--colour-bg);
    color: var(--colour-white);
    line-height: 1.7;
    background-image: url(/react-portfolio-1/static/media/night-sky-gif-1.70b38a42.gif)
}

.container {
    width: var(--container-width-big);
    margin: 0 auto
}

h1,
h2,
h3,
h4,
h5 {
    font-weight: 500
}

h1 {
    font-size: 2.5rem;
}

section {
    margin-top: 8 rem
}

section > h2, section > h5 {
    text-align: center;
    color: var(--colour-light)
}

section > h2 {
    color: var(--colour-primary);
    margin-bottom: 3rem;
}

.text-light {
    color: var(--colour-light)
}

a {
    color: var(--colour-primary);
    transition: var(--transition)
}

a:hover {
    color: var(--colour-white);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}
.nav {
  background: rgba(0,0,0,0);
  width: -webkit-max-content;
  width: max-content;
  display: block;
  padding: 0.7rem 1.7rem;
  z-index: 2;
  position: fixed;
  left: 15%;
  -webkit-transform: translate(50%);
          transform: translate(50%);
  bottom: 1rem;
  display: flex;
  grid-gap: 0.8rem;
  gap: 0.8rem;
  border-radius: 1rem;
  -webkit-backdrop-filter: blur(15px);
          backdrop-filter: blur(15px);
}

.nav a {
  background: transparent;
  padding: 0.9rem;
  border-radius: 50%;
  display: flex;
  color: white;
  font-size: 1.1rem;
}

.nav a:hover {
  background: rgba(0,0,0,3);
}

.nav a.active {
  background: var(--colour-bg);
  color: white
}

@media screen and (max-width: 1024px) {
  .nav {
    left: -18%
  }
}

@media screen and (max-width: 600px) {
  .nav {
    left: -45%
  }
}

.about {
    height: 200vh
}


.about_container {
    display: grid;
    grid-template-columns: 35% 50%;
    grid-gap: 15%;
    gap: 15%
}

.Headers {
    color: white;
    text-align: center;
    margin-top: 4rem;
}

.about_me {
    width: 90%;
    height: 45%;
    margin-top: 6rem;
    border-radius: 2rem;
    margin-left: 3rem;
    place-items: center;
    background: #c8f5ffa0;
    display: grid;
    place-items: center;
}

.about_me-image {
    

    border-radius: 2rem;
    margin-top: 1rem;
    overflow: hidden;
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
    transition: all 0.4s ease;
    
}

.about_me-image:hover {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg)
}

.about_cards {
    display: grid;
    margin-right: 2rem;
    margin-top: 5rem;
    border-radius: 2rem;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2rem;
    gap: 2rem;
}

.about_card {
    background: transparent;
    border-radius: 2rem;
    border: 1px solid white;
    padding: 1.3rem;
}

.about_card h5 {
    font-size: 1rem;
    color: white;
    text-align:center
}

.about_card small {
    color: white;
    margin-top: 4rem;
    font-size: 0.7rem;
    text-align: right;
}

.about_card:hover {
    background: rgb(32, 37, 92);
    transition: all ease-in-out 400ms;
    cursor: default;
}

.thing {
    text-align: center;
}

.icon {
    text-align: bottom;
}

.about_icon {
    color: black;
    font-size: 1.4rem;
    margin-bottom: 2rem;
    text-align: left;
}

.about_content p {
    margin: 4rem 2rem 2.6rem;
    color: white;
    text-align: left;
}

@media screen and (max-width: 1024px) {
    .about_container {
        grid-template-columns: 1fr;
        grid-gap: 0;
        gap: 0;
        margin-left: 2rem;
    }

    .about_me {
        width: 50%;
        margin: 7rem auto 4rem;
        height: 50%
    }

    .about_content p {
        margin: 3rem 3rem 1.5rem;
    }
}

@media screen and (max-width: 600px) {
    .about_me {
        width: 65%;
        margin: 4rem auto 3rem;
height: 60%;
    }

    .about_cards {
        grid-template-columns: 1fr 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }

    .about_content {
        text-align: center;
    }

    .about_content p {
        margin: 1.5rem 0;
    }
}
